
.FinalScreen {
        position: absolute;
        top: 0%;
        right: 50%;
        transform: translateX(50%);
        height: 100vh;
        width: 100vw;
        background-color: aliceblue;
        background-size: cover;
        background-repeat: no-repeat;
        padding: 0%;
        overflow-y: scroll;
        overflow-x: hidden;
        direction: rtl;
  }

  .blue-text-final {
font-size: 1.4em;
    margin-bottom: 2vh;
    direction: rtl;
    text-align: center;
    font-family: "Assistant";
    color: rgb(28, 180, 227);
    font-weight: 900;
    max-width: 100vw;
    padding-left: 5vw;
    padding-right: 5vw;
    margin-top: 17vh;
    }

    .grey-big-final{
        font-size: 1.2em;
        margin-bottom: 2vh;
        direction: rtl;
        text-align: center;
        font-family: "Assistant";
        color: rgb(89,89,89);
        font-weight: 900;
        max-width: 90vw;
        padding-left: 5vw; 
        padding-right: 5vw; 
        }


        .button-final{
            border: none;
            border-radius: 50px;
            width: 69vw;
            height: 8vh;
            position: absolute;
            font-size: 1.3em;
            font-family: "Assistant" ,Arial;
            font-weight: bold;
            color: rgb(255, 255, 255);
            padding: 1.5vw;
            right: 50%;
            transform: translateX(50%);
            margin-top: 3vh;
            background-color:rgb(28, 180, 227);
            box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.3); 
        
        }

        .bottom{
            position: absolute;
            margin-top: 15vh;

        }
        .input-label {
            display: block;
            margin-bottom: 10px; /* רווח בין ה-label ל-input */
          }
          
          .input-field {
            width: 60%;
            height: 5vh;
            margin: 1vh auto; /* מרכז ומוסיף מרווח בין שדות */
            display: block;
            padding: 0 1em;
            font-size: 1.2em;
            font-family: "Assistant", Arial;
            text-align: center;

            border-radius: 25px; /* עיגול עדין */
            border: 1px solid #ccc; /* גבול דק */
            background-color: #f7f9fc; /* צבע רקע בהיר מודרני */
            box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); /* צל קל */
            transition: all 0.3s ease; /* אנימציה חלקה לפוקוס */
          }

          .input-field:focus {
            outline: none;
            border: 2px solid #1ca3d8; /* צבע כחול מודגש */
            box-shadow: 0 4px 10px rgba(28, 163, 216, 0.3); /* צל קל עם צבע הכחול */
            background-color: #fff; /* רקע לבן נקי */
          }

          .button-final:disabled {
            background: linear-gradient(135deg, #b5b5b5, #5c5c5c); /* צבעים "כבויים" */
            cursor: not-allowed; /* סמן של איסור */
            box-shadow: none; /* בלי הצללה */
            opacity: 0.7; /* שקיפות קלה */
            color: #f8f8f8; /* טקסט אפור בהיר */
          }


