.Society {
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color:  aliceblue;
    z-index: 2000;
    padding-top: 2vh;
    overflow-y: scroll;
    text-align: right;
    direction: rtl;
}

#next-btn-soc {
position: absolute;
z-index: 5;
margin-top: 3vh;
right: 47%;
height: 8vh;
width: auto;
cursor: pointer;
}

.blue-text-soc {
font-size: 1.4em;
margin-bottom: 2vh;
direction: rtl;
text-align: right;
font-family: "Assistant" ,Arial;
font-weight:900;
color:rgb(28, 180, 227);
width: 95vw;
padding-left: 5vw;
padding-right: 5vw;
}

.titles-gray-soc {
    font-size: 2.8em;
    margin-top: 14vh;
    margin-bottom: 2vh;
    direction: rtl;
    text-align: right;
    font-family: "karantina";
    color: rgb(89, 89, 89);
    text-align: right;
    padding-left: 5vw;
    padding-right: 5vw;
}

.simple-text-soc {
width: 100%;
height: auto;
margin-bottom: 2vh;
font-family: "Assistant" ,Arial;
font-size: 1.2em;
text-align: right;
padding-left: 5vw;
padding-right: 5vw;
max-width: 92vw;
color: rgb(89,89,89);
}

.image-content-soc {
width: 100%;
height: auto;
margin-bottom: 2vh;
}

.grey-bold-soc{
font-size: 1.2em;
margin-bottom: 2vh;
direction: rtl;
text-align: center;
font-family: "Assistant" ,Arial;
color: rgb(89,89,89);
font-weight: 900;
max-width: 92vw;
padding-left: 5vw; 
padding-right: 5vw; 
}

.footer-soc {
    position: absolute;
    right: 50%;
    height: 10vh;
    transform: translateX(50%);
    background-color: transparent;
    font-weight: bold;
    margin-top: 13vh;
    margin-bottom: 22vh;
    width: 90vw;
    font-size: 1.1em;
}


@media screen and (min-width: 768px) {
   .Society {
        position: relative;
        width: 100vw;
          overflow-y: scroll; 
  overflow-x: hidden;
     scrollbar-width: none;
    -ms-overflow-style: none; 
  }
 .Society::-webkit-scrollbar{
     display: none; 
 }

#next-btn-soc:hover {
transform: scale(1.2);
}

